import {Component, OnInit} from '@angular/core';
import {Offer, StartService} from "../service/start.service";

@Component({
  selector: 'app-offers',
  templateUrl: './offers.component.html',
  styleUrls: ['./offers.component.css']
})
export class OffersComponent implements OnInit {
  offers: Offer[];
  kategorie: string;

  constructor(public service:StartService) { }

  ngOnInit(): void {
    this.service.getPenny().subscribe(value => {
      this.offers = value;
      this.offers = this.offers.sort((a, b) =>(a.kategorie<b.kategorie ? -1 : 1)  )
    })
  }

  setKegorie(kategorie: string) {
    this.kategorie = kategorie;
  }
}
